import React from "react";
import "./Footer.css";
import PropTypes from "prop-types";
import { gitHubDetails } from "../../config.js";
function Footer({ dark_mode }) {
    return (
        <div className={dark_mode ? "dark_mode_footer" : "light_mode_footer"}>
            <div className="footer">
                &copy; {new Date().getFullYear()} Copyright:
                <a
                    rel="noopener noreferrer"
                    href={gitHubDetails.profileLink}
                    target="_blank"
                >
                    {" " + gitHubDetails.username}
                </a>
            </div>
        </div>
    );
}

Footer.propTypes = {
    dark_mode: PropTypes.bool.isRequired,
};

export default Footer;
